___CSS_LOADER_EXPORT___.push([module.id, ".bottom-icon{display:inline-block;padding:.5em 1em;text-decoration:none;background:#6f9;color:#fff;border-bottom:4px solid #6c6;border-radius:3px}.bottom-icon:active{transform:translateY(4px);box-shadow:0 0 1px rgba(0,0,0,.2);border-bottom:none}", "",{"version":3,"sources":["webpack://./resources/js/components/test/period_page.vue"],"names":[],"mappings":"AAwGA,aACA,oBAAA,CACA,gBAAA,CACA,oBAAA,CACA,eAAA,CACA,UAAA,CACA,4BAAA,CACA,iBACA,CAEA,oBAGA,yBAAA,CACA,iCAAA,CACA,kBACA","sourcesContent":["<template>\n  <div>\n    <vue-loading v-if=\"is_loading\" type=\"spin\" color=\"#333\" :size=\"{ width: '50px', height: '50px' }\"></vue-loading>\n    <div v-else class=\"container\">\n      <!-- 保存 -->\n      <div class=\"text-center\"> \n        <button class=\"bottom-icon btn mb-3\" @click=\"save\">\n          保存\n        </button>\n      </div>\n\n      <div class=\"card\" v-for=\"(period, index) in periods\">\n        <div class=\"card-header d-flex justify-content-between\">\n          <div class=\"row\">\n            <datetime type=\"date\" zone=\"Asia/Tokyo\" v-model=\"period.start\" /> \n            ~\n            <datetime type=\"date\" v-model=\"period.end\" />\n          </div>\n          <div>\n            <input type=\"number\" v-model=\"period.goal_point\" />pt\n          </div>\n          <button class=\"btn block p-0\">\n            <span class=\"far fa-trash-alt\" @click=\"deletePeriod(index)\"/>\n          </button>\n        </div>\n      </div>\n\n      <!-- 追加ボタン -->\n      <button \n        class=\"btn block\"\n        @click=\"addPeriod()\"\n      >\n        <span style=\"font-size:20px;\" class=\"far fa-plus-square\"/>\n      </button>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { VueLoading } from 'vue-loading-template'\n\nexport default {\n  components:{\n    VueLoading,\n  },\n  data(){\n    return{\n      is_loading: false,\n      periods: [],\n      delete_periods: [],\n    };\n  },\n  created(){\n    this.init();\n  },\n  methods:{\n    init(){\n      this.is_loading = true;\n\n      axios\n        .get('/api/period/index')\n        .then((response) => {\n          this.is_loading = false;\n          this.periods = response.data.periods;\n      });\n    },\n    save(){\n      this.is_loading = true;\n      const periods = this.periods;\n      const delete_periods = this.delete_periods;\n\n      axios\n        .post('/api/period/update', {periods, delete_periods})\n        .then((response) => {\n          this.is_loading = false;\n          this.$toasted.success('更新しました');\n          this.periods = response.data.periods;\n      }).catch((error) =>{\n        this.is_loading = false;\n        this.$toasted.error('更新出来ませんでした');\n      });\n    },  \n    deletePeriod(index){\n      const item = this.periods[index];\n      if(!item.hasOwnProperty('is_new')){\n        this.delete_periods.push(item);\n      }\n\n      this.$delete(this.periods, index);\n    },\n    addPeriod(){\n      const new_period = {\n        'is_new': true,\n        'start': null,\n        'end': null,\n      };\n\n      this.periods.push(new_period);\n    },\n  },\n}\n</script>\n\n<style>\n.bottom-icon {\n  display: inline-block;\n  padding: 0.5em 1em;\n  text-decoration: none;\n  background: #66FF99;/*ボタン色*/\n  color: #FFF;\n  border-bottom: solid 4px #66CC66;\n  border-radius: 3px;\n}\n\n.bottom-icon:active{\n  /*ボタンを押したとき*/\n  -webkit-transform: translateY(4px);\n  transform: translateY(4px);/*下に動く*/\n  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.2);/*影を小さく*/\n  border-bottom: none;\n}\n</style>"],"sourceRoot":""}]);
